.card__edit-button {
  width: 18px;
  height: 18px;
  background: transparent url('../../../images/edit-icon.svg') center no-repeat;
  /*Потом это будет из модификатора*/
  background-size: 18px 18px;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 19px;
  right: 42px;
  padding: 0;
  margin: 0;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.card__edit-button:hover {
  opacity: 0.6;
}
